.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dropdown {
  margin-top: 20px; 
  margin-bottom: 20px; 
  display: flex; 
  align-items: center; 
}

.time_slider {
  margin-bottom: 20px; 
  display: flex; 
  align-items: center; 
}

.time_slider label {
  margin-right: 10px; 
  white-space: nowrap; 
}

.footnote {
  font-size: 0.75em; 
  color: #666;
  margin-top: 5px; 
  margin-bottom: 5px; 
}